import Vue from 'vue'
import Router from 'vue-router'
import firstValidate from '@/pages/firstValidate/firstValidate'
import setCompany from '@/pages/firstValidate/setCompany'
import setOwner from '@/pages/firstValidate/setOwner'
import foodSaList from '@/pages/firstValidate/foodSaList'
import setFoodSa from '@/pages/firstValidate/setFoodSa'
const foodSa = () => import('@/pages/foodSa/foodSa')
const foodSaValidate = () => import('@/pages/foodSaValidate')
const foodSaDetail = () => import('@/pages/foodSa/foodSaDetail')
const foodSaIndex = () => import('@/pages/foodSa/foodSaIndex')
const foodSaHome = () => import('@/pages/foodSa/home/home')
const foodSaTask = () => import('@/pages/foodSa/task/task')
const foodSaMsg = () => import('@/pages/foodSa/msg/msg')
const foodSaMe = () => import('@/pages/foodSa/me/me')

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/firstValidate',
      name: 'firstValidate',
      component: firstValidate,
      redirect: '/firstValidate/setCompany',
      children: [
        {
          path: '/firstValidate/setCompany',
          name: '企业信息验证',
          component: setCompany
        },
        {
          path: '/firstValidate/setOwner',
          name: '法人信息验证',
          component: setOwner
        },
        {
          path: '/firstValidate/foodSaList',
          name: '食安员列表',
          component: foodSaList
        },
        {
          path: '/firstValidate/setFoodSa',
          name: '食安员配置',
          component: setFoodSa
        }
      ]
    },
    {
      path: '/',
      name: 'foodSaValidate',
      component: foodSaValidate
    },
    {
      path: '/foodSa',
      name: 'foodSa',
      component: foodSa,
      redirect: '/foodSa/foodSaDetail',
      children: [
        {
          path: '/foodSa/foodSaDetail',
          name: 'foodSaDetail',
          component: foodSaDetail
        },
        {
          path: '/foodSa/foodSaIndex',
          name: 'foodSaIndex',
          component: foodSaIndex,
          redirect: '/foodSa/foodSaIndex/foodSaHome',
          children: [
            {
              path: 'foodSaHome',
              name: 'foodSaHome',
              component: foodSaHome
            },
            {
              path: 'foodSaTask',
              name: 'foodSaTask',
              component: foodSaTask
            },
            {
              path: 'foodSaMsg',
              name: 'foodSaMsg',
              component: foodSaMsg
            },
            {
              path: 'foodSaMe',
              name: 'foodSaMe',
              component: foodSaMe
            }
          ]
        }
      ]
    }
  ]
});

export default router



